<template>

  <div>
    <div v-for="(data, index) in come_counts" :key="index" class="item_main">
      <div>{{data.date}}</div>

      <div style="margin-left: 20px">{{data.count}}次</div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "BaseDateShow",
        data() {
            return {
                come_counts: [],
                last_location: '',
            }
        },
        mounted() {
            const query = Bmob.Query(this.Common_Base.SQL_Pahsa_Base);
            query.find().then((res) => {

                this.come_counts.length = 0
                for (let item of res) {
                    this.come_counts.push({'date': item['date'], 'count': item['count']})
                }

            })

        }
    }
</script>

<style scoped>

  .item_main{
    display: flex;
  }
</style>



